
export const AXIOS_CONFIG = function() {
    return {
	    baseURL: `${ppwp_data.api_url}wppp/v1/`,
        timeout: 10000, //milisecond
        headers: {
            'Cache-Control': 'no-cache, no-store, must-revalidate',
            'Pragma': 'no-cache',
            'Expires': '0',
            'X-WP-Nonce': ppwp_data.nonce
        }
    }
}

export const API_CONFIGS = {
    DATA_PASSWORD: (id) => `get-data-password/${id}`,
    CREATE_NEW_PASSWORD: (id) => `add-new-password/${id}`,
    FILE_IS_PROTECT: (id) => `file-is-protect/${id}`,
    PROTECT_THIS_FILE: (id) => `protect-this-file/${id}`,
    UN_PROTECT_THIS_FILE: (id) => `un-protect-this-file/${id}`,
}



