import {ACTIONS} from '../actions';

const STATE_DATA_PASSWORD = {
    data: [],
};

export function reducerDataPassword(state = STATE_DATA_PASSWORD, action) {
    switch (action.type) {
        case ACTIONS.DATA_PASSWORD:
            if (action.payload.status === 200) {
                return {...state, data: action.payload.data};
            } else {
                return {...state, data: []}
            }
        default:
            return state;
    }
}

const INITIAL_NEW_PASSWORD_STATE = {
    isError: true,
    message: 'Opps! Unable to create new password. Please try again or contact plugin owner.'
};

export function reducerCreateNewPassword(state = INITIAL_NEW_PASSWORD_STATE, action) {
    switch (action.type) {
        case ACTIONS.CREATE_NEW_PASSWORD:
            if (action.payload.status === 200) {
                return {...state, isError: false, message: action.payload.data.message};
            }
            return {...state, isError: true, message: action.payload.response.data.message};
        default:
            return state;
    }
}

const STATE_PROTECTED_FILE = {
    post: null,
    file_is_protected: false,
};

export function isProtectedFile(state = STATE_PROTECTED_FILE, action) {
    switch (action.type) {
        case ACTIONS.FILE_IS_PROTECT:
            if (action.payload.status === 200) {
                return {...state, post: action.payload.data.post, file_is_protect: action.payload.data.file_is_protect};
            } else {
                return {...state, post: null, file_is_protected: false}
            }
        default:
            return state;
    }
}

const INITIAL_STATE_PROTECT_FILE = {
    response: false
};

export function protectFile(state = INITIAL_STATE_PROTECT_FILE, action) {
    switch (action.type) {
        case ACTIONS.PROTECT_FILE:
            if (action.payload.status === 200) {
                return {...state, response: action.payload.data};
            } else {
                return {...state, response: action.payload.response.data.message, error: true}
            }
        default:
            return state;
    }
}

const INITIAL_STATE_UNPROTECT_FILE = {
    response: false
};

export function unProtectFile(state = INITIAL_STATE_UNPROTECT_FILE, action) {
    switch (action.type) {
        case ACTIONS.UNPROTECT_FILE:
            if (action.payload.status === 200) {
                return {...state, response: action.payload.data};
            } else {
                return {...state, response: action.payload.response.data.message, error: true}
            }
        default:
            return state;
    }
}



