import {combineReducers} from 'redux';
import {reducer as formReducer} from 'redux-form';
import {reducer as toastrReducer} from 'react-redux-toastr';
import {reducerDataPassword, reducerCreateNewPassword, isProtectedFile, protectFile, unProtectFile} from './reducers-password-metabox';

const rootReducer = combineReducers({
    form: formReducer,
    toastr: toastrReducer,
    reducerDataPassword: reducerDataPassword,
    reducerCreateNewPassword: reducerCreateNewPassword,
    isProtectedFile: isProtectedFile,
    protectFile: protectFile,
    unProtectFile: unProtectFile,
});

export default rootReducer;



