import React, {Component} from 'react';
import './protect-or-unprotect.scss';
import 'react-select/dist/react-select.css';

class ProtectOrUnprotect extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const { isProtected, handler } = this.props;
        return (
            <div className="pda_mi_wrap_protect_post">
                <label className="pda_mi_switch">
                    <input type="checkbox" checked={isProtected} onChange={handler}/>
                    <span id="pda_mi_protect_post" className="pda_mi_slider pda_mi_round"></span>
                </label>
                <label htmlFor="pda_mi_protect_post" className="pda_mi_label_password_protect">Password protected</label>
            </div>
        );
    }
}

export default ProtectOrUnprotect;



