import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import store from './store';
import {connect} from 'react-redux';
import {Provider} from 'react-redux';
import {bindActionCreators} from 'redux';
import {toastr} from 'react-redux-toastr';
import ReduxToastr from 'react-redux-toastr';
import 'react-redux-toastr/src/styles/index.scss';
import PasswordForm from './components/password-form';
import ProtectOrUnprotect from './components/protect-or-unprotect';
import {actionDataPassword, fileIsProtected, protectThisFile, unProtectThisFile} from './actions/password-metabox';
import {MESSAGES} from "./utils/strings";
import './index.css';

class App extends Component {
    constructor(props) {
        super(props);
        this.checkPostProtected = this.checkPostProtected.bind(this);
        this.state = {
            currentPostId: null,
            loadingData: false,
            isProtected: false,
            isLoading: false,
        }
    }

    render() {
        const {responseDataPassword} = this.props;
        const {isProtected, currentPostId, isLoading} = this.state;
        const roleNumber = responseDataPassword.data.length;
        let roleText;
        if (roleNumber < 2) {
            roleText = (<p><b>{roleNumber} role</b> can access your protected content</p>);
        } else {
            roleText = (<p><b>{roleNumber} roles</b> can access your protected content</p>);
        }

        const urlButtonLoading = ppwp_data.plugin_url + "admin/images/pda-loading.gif";
        const btnLoading = (
            <div className="ppwp_icon_loading">
                <img src={urlButtonLoading}/>
            </div>
        );
        const elementProtected = isProtected ? (
            <div>
                {roleText}
                <PasswordForm postId={currentPostId} rolesSelected={responseDataPassword.data}/>
            </div>
        ) : '';

        return (
            <div>
                <ReduxToastr
                    timeOut={2000}
                    newestOnTop={false}
                    preventDuplicates={true}
                    position="top-center"
                    transitionIn="fadeIn"
                    transitionOut="fadeOut"
                />
                <ProtectOrUnprotect isProtected={isProtected} postId={currentPostId} handler={this.handleProtectPost}/>
                {isLoading ? btnLoading : elementProtected}
            </div>
        );
    }

    checkPostProtected() {
        const {currentPostId} = this.state;
        const {fileIsProtected} = this.props;
        fileIsProtected(currentPostId).then(() => {
            const {fileStatus} = this.props;
            this.setState(...this.state, {
                isProtected: fileStatus.file_is_protect === 'true' ? true : false,
                loadingData: false,
            });
        });
    }

    handleProtectPost = (evt) => {
        this.setState(...this.state, {
            isLoading: true
        });

        const statusFile = evt.target.checked;
        const {currentPostId} = this.state;
        const {protectThisFile, unProtectThisFile, actionDataPassword} = this.props;
        if (statusFile) {
            protectThisFile(currentPostId)
                .then(() => {
                    const {protectFile} = this.props;
                    if (protectFile.error) {
                        toastr.error(protectFile.response);
                    } else {
                        // Fetch data when protect file
                        actionDataPassword(currentPostId);
                        this.setState(...this.state, {
                            isProtected: statusFile,
                            isLoading: false
                        });
                        toastr.success(MESSAGES.PROTECT_FILE);
                    }
                });
        } else {
            unProtectThisFile(currentPostId)
                .then(() => {
                    const {unProtectFile} = this.props;
                    if (unProtectFile.error) {
                        toastr.error(protectFile.response);
                    } else {
                        toastr.success(MESSAGES.UN_PROTECT_FILE);
                        this.setState(...this.state, {
                            isProtected: statusFile,
                            isLoading: false
                        });
                    }
                });
        }
    }

    componentWillMount() {
        const currentPostId = jQuery("#ppwp_password_protect_by_roles").attr('class').split('_')[1];
        this.setState(...this.state, {
            currentPostId,
        });
        const {actionDataPassword} = this.props;
        actionDataPassword(currentPostId);
    }

    componentDidMount() {
        this.checkPostProtected();
    }
}

const mapStateToProps = state => {
    return {
        responseDataPassword: state.reducerDataPassword,
        fileStatus: state.isProtectedFile,
        protectFile: state.protectFile,
        unProtectFile: state.unProtectFile,
    }
}

const mapDispatchToProps = dispatch => {
    return bindActionCreators({
        actionDataPassword,
        fileIsProtected,
        protectThisFile,
        unProtectThisFile
    }, dispatch);
}

const PPWPApp = connect(mapStateToProps, mapDispatchToProps)(App);

ReactDOM.render(
    <Provider store={store}>
        <PPWPApp/>
    </Provider>,
    document.getElementById("ppwp_password_protect_by_roles")
);



