import axios from 'axios';
import { AXIOS_CONFIG } from '../configs';
import { ACTIONS } from './index';
import { API_CONFIGS } from "../configs";

export function actionDataPassword(postId) {
    const axiosInstance = axios.create(AXIOS_CONFIG());
    const request = axiosInstance.get(API_CONFIGS.DATA_PASSWORD(postId));

    return {
        type: ACTIONS.DATA_PASSWORD,
        payload: request
    }
}

export function actionCreateNewPassword(postId, data) {
    const axiosIntance = axios.create(AXIOS_CONFIG());
    const request = axiosIntance.post(API_CONFIGS.CREATE_NEW_PASSWORD(postId), data);

    return {
        type: ACTIONS.CREATE_NEW_PASSWORD,
        payload: request,
    }
}

export function fileIsProtected(postId) {
    const axiosInstance = axios.create(AXIOS_CONFIG());
    const request = axiosInstance.get(API_CONFIGS.FILE_IS_PROTECT(postId));

    return {
        type: ACTIONS.FILE_IS_PROTECT,
        payload: request
    }
}

export function protectThisFile(postId) {
    const axiosIntance = axios.create(AXIOS_CONFIG());
    const request = axiosIntance.post(API_CONFIGS.PROTECT_THIS_FILE(postId));

    return {
        type: ACTIONS.PROTECT_FILE,
        payload: request
    }
}

export function unProtectThisFile(postId) {
    const axiosIntance = axios.create(AXIOS_CONFIG());
    const request = axiosIntance.post(API_CONFIGS.UN_PROTECT_THIS_FILE(postId));

    return {
        type: ACTIONS.UNPROTECT_FILE,
        payload: request
    }
}



