import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {MESSAGES} from '../../utils/strings';
import './password-form.scss';
import Select from 'react-select';
import 'react-select/dist/react-select.css';
import {actionCreateNewPassword} from '../../actions/password-metabox';
import {actionDataPassword} from '../../actions/password-metabox';
import {toastr} from 'react-redux-toastr';

class PasswordForm extends Component {
	constructor(props) {
		super(props);
		this.onSubmit = this.onSubmit.bind(this);
		this.state = {
			loadingData: false,
			typeSelected: {value: 'global', label: 'Global'},
			roleSelected: [],
			password: "",
			currentRole: "",
			disableButton: false,
			valueButton: "Submit",
			addNew: false,
			showPasswordForRoles: "",
		};

	}

	render() {
		const {typeSelected, roleSelected, disableButton, valueButton, currentRole, addNew, showPasswordForRoles} = this.state;
		const {rolesSelected} = this.props;

		const options = [
			{value: 'global', label: 'Global'},
			{value: 'roles', label: 'Roles'},
		];

		const roles = ppwp_data.roles.map((role) => {
			return {
				label: role,
				value: role
			}
		});

		const selectList = rolesSelected.map((role, key) => {
			const roleArray = role.campaign_app_type.split(";");
			const roles = roleArray.map(function (role) {
				return role.replace("Role_", "");
			});
			const ppwpColor = roles.join(";") == currentRole ? "ppwp_role_user_click" : "";
			return (
				<span title="Click to view more" key={key} onClick={this.handleClickRole} ppwp_password={role.password}
				      className={ppwpColor}>{roles.join(", ")} </span>);
		});

		const textForGlobalPassword = showPasswordForRoles.split(", ").length > 1 ? 'Global passwords:' : 'Global password:';
		const massagePassword = showPasswordForRoles.split(", ").map(pass => '<code>' + pass + '</code>&nbsp;');
		const typePasswordSetected = 'global' === currentRole ? (
			<p>{textForGlobalPassword} <b dangerouslySetInnerHTML={{__html: massagePassword.join('')}}/></p>
		) : (
			<p>Password for {currentRole.split(";").join(", ")}: <b
				dangerouslySetInnerHTML={{__html: massagePassword.join('')}}/></p>
		);

		const showPassword = (
			showPasswordForRoles !== "" ? typePasswordSetected : ""
		);

		const formAddNewPassword = (
			<form className="ppwp_wrap_password_form" onSubmit={this.onSubmit}>
				<Select
					className="ppwp_type_metabox"
					name="ppwp_password_metabox"
					clearable={false}
					value={typeSelected}
					onChange={this.handleChangeType}
					placeholder="Select type..."
					options={options}
				/>
				{typeSelected.value == "roles" &&
				<div>
					<Select
						className="ppwp_roles_metabox"
						name="ppwp_roles_metabox"
						multi
						onChange={this.handleChangeRoles}
						options={roles}
						placeholder="Select roles..."
						value={roleSelected}
						required={true}
					/>
				</div>
				}
				<input placeholder="Enter password..." className="ppwp_password" onChange={this.handlePasswordChange}
				       type="text" name="ppwp_password" required/>
				<div className="ppwp_password_btn">
					<button className="button action" type="submit" disabled={disableButton}>{valueButton}</button>
					{addNew ? <a className="ppwp_button_hide" onClick={this.handleAddNewPassword}>Hide</a> : null}
				</div>
			</form>
		);

		const form = (
			<div className="ppwp_password_form">
				<p className="ppwp_wrap_group_roles">{selectList}</p>
				{showPassword}
				{addNew && formAddNewPassword}
				{!addNew && <a className="ppwp_btn_addnew_form" onClick={this.handleAddNewPassword}>Add new</a>}
			</div>
		);

		return this.state.loadingData ? this.renderLoading() : form;
	}

	handleAddNewPassword = () => {
		this.setState(...this.state, {addNew: !this.state.addNew});
	}

	onSubmit(event) {
		event.preventDefault();
		const {typeSelected, roleSelected, password, currentRole, showPasswordForRoles} = this.state;
		if (password.indexOf(" ") !== -1) {
			toastr.error(MESSAGES.PPWP_ERROR_SPACE_PASSWORD);
			return;
		}
		const values = {};
		values.ppwp_password = password;
		values.typeSelected = typeSelected.value;
		values.roleSelected = this.handleTypeSelected(roleSelected);
		const {postId, actionCreateNewPassword, actionDataPassword} = this.props;
		this.setState(...this.state, {
			disableButton: true,
			valueButton: "Submitting"
		});
		actionCreateNewPassword(postId, values)
			.then(() => {
				const {message, isError} = this.props.createNewPasswordResponse;
				if (isError) {
					toastr.error(message);
				} else {
					toastr.success(message);
					actionDataPassword(postId);
					this.setState(...this.state, {
						addNew: !this.state.addNew,
						roleSelected: [],
						typeSelected: {value: 'global', label: 'Global'}
					});
					const roleSelectedString = roleSelected.map((role) => {
						return role.value;
					});

					if (currentRole !== "" && (currentRole === roleSelectedString.sort().join(";")) || currentRole === 'global') {
						this.setState(...this.state, {showPasswordForRoles: showPasswordForRoles + ", " + password});
					}

				}
				this.setState(...this.state, {
					disableButton: false,
					valueButton: "Submit",
				});
			});
	}

	handlePasswordChange = (event) => {
		this.setState(...this.state, {password: event.target.value});
	}

	handleClickRole = (values) => {
		const roles = values.target.innerHTML.split(", ");
		const ppwpPassword = values.target.getAttribute("ppwp_password");
		const roleArray = roles.map((role) => {
			return role.replace(" ", "")
		});
		this.setState(...this.state, {
			showPasswordForRoles: ppwpPassword,
			currentRole: roleArray.join(";")
		});
	}

	handleChangeType = (typeSelected) => {
		this.setState(...this.state, {typeSelected});
	}

	handleChangeRoles = (roleSelected) => {
		this.setState(...this.state, {roleSelected});
	}

	renderLoading() {
		return (
			<div className="loading">Loading Data...</div>
		)
	}

	handleTypeSelected = (roleSelected) => {
		return roleSelected.map((mem) => {
			return mem.value
		});
	}

}

const mapActionsToProps = (dispatch) => {
	return bindActionCreators({
		actionCreateNewPassword,
		actionDataPassword,
	}, dispatch);
}

const mapStateToProps = (state) => {
	return {
		createNewPasswordResponse: state.reducerCreateNewPassword
	}
}

export default connect(mapStateToProps, mapActionsToProps)(PasswordForm);



